.hero-section {
    display: flex;
    align-items: center;
    padding: 163px 96px;
    border-radius: 0 0 30px 30px;
}

.hero-title-wrap {
    width: 517px;
}
.hero-title {
    font-family: Helvetica Neue;
    font-size: 70px;
    font-weight: 500;
    line-height: 70px;
    /* letter-spacing: -3%; */
    margin: 0;
    margin-bottom: 28px;    
}
.hero-text {
    font-family: Helvetica Neue;
    font-size: 28px;
    font-weight: 400;
    line-height: 30px;
    /* letter-spacing: -2%; */
    margin: 0;
    margin-bottom: 64px;
}
.hero-btn {
    display: inline-flex;
    align-items: center;

    color: #fbfbfb;
    font-family: Helvetica Neue;
    font-size: 20px;
    font-weight: 500;
    line-height: 24px;
    text-decoration: none;
    /* letter-spacing: -1%; */
    padding: 18px 50px;
    box-sizing: border-box;
    border: 1px solid #fbfbfb66;
    border-radius: 30px;
    background-color: transparent;
}

.arrow-icon {
    width: 18px;
    height: 18px;
    fill: #FBFBFB; 
    margin-left: 18px;
  }

  .img {
    flex-shrink: 0;
  }

  .hero-image {
    position: relative;
    top: 0%;
    left: 0%;
    max-width: 100%; 
    border-radius: 0px 30px 30px 0px;

  }

  .hero-section-add-info {
    display: flex;
    gap: 16px;
    background-color: #FBFBFB;
    color: #11101c80;
    border-radius: 20px;
    padding: 32px;

    font-family: Helvetica Neue;
    font-size: 16px;
    line-height: 19px;

    
    position: absolute;
    bottom: 50px;
    right: 50px;
}

.check-wrap {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #103931;
    padding: 12px;
    border-radius: 13px;
    width: 54px;
    height: 54px;

  }

  .check-icon {
    display: inline-block;
    width: 20px;
    height: 15px;
    fill: #FBFBFB; 
  }

.add-info-text-wrap {
    display: flex;
    flex-direction: column;
    gap: 6px;
}

  .add-info-text {
    margin: 0;
    color: #11101c80;
    font-family: Helvetica Neue;
    font-size: 16px;
    font-weight: 400;
    line-height: 19px;
  }

  .add-info-amount {
    margin: 0;
    color: #11101c;
    font-family: Helvetica Neue;
    font-size: 24px;
    font-weight: 700;
    line-height: 29px;
  }